// import createPersistedState from "vuex-persistedstate"

export default {
  namespaced: true,
  state: {
    // 
    // plugins: [createPersistedState({
    // 	storage: window.sessionStorage
    // })],
    audio: [], //
    audition: [], //
    musicAudioStatu: 0, //audio,audition
    alplayShow: true,
  },
  actions: {},
  mutations: {
    //
    addSONG(state, song) {
      for (let item of state.audio) {
        if (item.id === song.id) {
          this._vm.$message("");
          return;
        }
      }
      state.audio.push(song); //
      this._vm.$message({
        type: "success",
        message: "",
      });
    },
    //
    deleteMUSIC(state, index) {
      state.audio.splice(index, 1);
      this._vm.$message({
        type: "success",
        message: "",
      });
    },
    //
    addAUDITION(state, song) {
      for (let item of state.audition) {
        if (item.id === song.id) {
          this._vm.$message("");
          return;
        }
      }
      state.musicAudioStatu = 1;
      state.audition.splice(0, 1);
      state.audition[0] = song; //
      this._vm.$message({
        type: "success",
        message: "",
      });
    },
    // 
    deleteAUDITION(state, index) {
      state.audition.splice(index, 1);
      this._vm.$message({
        type: "success",
        message: "",
      });
    },
    //
    changeAPLAYER(state, statu) {
      statu === 1 ? (state.musicAudioStatu = 1) : (state.musicAudioStatu = 0);
    },
  },
};
