import { cloudMusic } from '@/api/request'

//key
export function getqrKey(params) {
    return cloudMusic({
        method: 'get',
        url: '/login/qr/key',
        params: params,
    })
}
//key
export function getqrImg(params) {
    return cloudMusic({
        method: 'get',
        url: '/login/qr/create',
        params: params,
    })
}
//
export function getqrStatus(params) {
    return cloudMusic({
        method: 'post',
        url: '/login/qr/check',
        data: params,
    })
}
