import axios from 'axios'
import { Message } from 'element-ui'
import loginUrl from './needLogin/loginUrl';
import { getCookie } from '@/utils/cookie';
// import { getToken } from '@/utils/auth'
// import { MessageBox, Message } from 'element-ui'



//axois
const cloudMusic = axios.create({
    baseURL: 'https://biejuanle.icu/',
    // baseURL: 'http://localhost:3000/',
    // changeOrigin: true,  //
    withCredentials: true,
});
const cloudMusic2 = axios.create({
    baseURL: 'https://biejuanle.icu/',
    // baseURL:'http://localhost:3000/'
    // changeOrigin: true,  //
    withCredentials: true,
});

//axios
// axios.defaults.baseURL = ''
axios.defaults.timeout = 5000

// 
// Add a request interceptor
cloudMusic.interceptors.request.use(config => {
    if (loginUrl.includes(config.url) && !localStorage.getItem("user")) {//url
        Message.error('')
        return false
    }
    return config
}, error => {
    // Do something with request error
    return Promise.reject(error);
});
// Add a response interceptor
cloudMusic.interceptors.response.use(response => {

    return response;
}, error => {
    Message.error('')
    return Promise.reject(error);
});
export { cloudMusic, cloudMusic2 };//import { cloudMusic, cloudMusic2 } from  ...
