/* //
 :  ,  ,  ,  , 
  `keywords` : 
  `type` :  'mobile' 
*/
export function getSearchSuggest(params) {
    return cloudMusic({
        url: '/search/suggest',
        method: 'get',
        params: params,
    })
}

