import { playMusicUrl, getSongLyric } from "@/api/api";

//
export function playMusic(songDetals, that) {
  //
  // console.log(songDetals, "----");
  let songlrc = ""; //
  if (songDetals.timer) {
    if (songDetals.noCopyrightRcmd) {
      that.$message({
        message: "",
        type: "error",
      });
      songDetals.timer = false;
      setTimeout(() => {
        songDetals.timer = true;
      }, 3000);
      return;
    }
    if (songDetals.fee == 1) {
      that.$message({
        message: "VIP - 30s",
        type: "warning",
      });
    }
    // 
    // getSongLyric({
    //   id: id,
    // }).then((res) => {
    //   songlrc = res.data.lrc.lyric;
    // }),
    that.$store.commit("aplayer/addSONG", {
      name: songDetals.name, //
      artist: songDetals.ar[0].name, //
      // url: songUrlAdd,
      url: `https://music.163.com/song/media/outer/url?id=${songDetals.id}.mp3`,
      cover: songDetals.al.picUrl,
      //   lrc: songlrc,
      id: songDetals.id,
    });
    songDetals.timer = false;
    setTimeout(() => {
      songDetals.timer = true;
    }, 3000);
  }
}

//
export function listenMusic(songDetals, that) {
  // console.log(songDetals, "----");
  let songUrlAdd = null;
  let songlrc = "";
  if (songDetals.timer) {
    if (songDetals.noCopyrightRcmd) {
      that.$message({
        message: "",
        type: "error",
      });
      songDetals.timer = false;
      setTimeout(() => {
        songDetals.timer = true;
      }, 3000);
      return;
    }
    if (songDetals.fee == 1) {
      that.$message({
        message: "VIP - 30s",
        type: "warning",
      });
      playMusicUrl({
        id: songDetals.id,
      }).then(async (res) => {
        // console.log("", res.data.data[0].url);
        if (res.data.data[0].url) {
          songUrlAdd = await res.data.data[0].url;
          if (songUrlAdd != null) {
            that.$store.commit("aplayer/addAUDITION", {
              name: songDetals.name, //
              artist: songDetals.ar[0].name, //
              url: songUrlAdd,
              cover: songDetals.al.picUrl,
              // lrc: songlrc,
              id: songDetals.id,
              timer: true, //
            });
          }
        }
      });
      // 
      //   getSongLyric({
      //     id: id,
      //   }).then((res) => {
      //     songlrc = res.data.lrc.lyric;
      //   });
    } else if (songDetals.fee == 0 || songDetals.fee == 8) {
      that.$store.commit("aplayer/addAUDITION", {
        name: songDetals.name, //
        artist: songDetals.ar[0].name, //
        // url: songUrlAdd,
        url: `https://music.163.com/song/media/outer/url?id=${songDetals.id}.mp3`,
        cover: songDetals.al.picUrl,
        // lrc: songlrc,
        id: songDetals.id,
        timer: true, //
      });
    }
    songDetals.timer = false;
    setTimeout(() => {
      songDetals.timer = true;
    }, 3000);
  }
}
